import uniIcon from "@/components/uni-icons/uni-icons.vue";
export default {
	components:{
		uniIcon
	},
	filters:{
		showStar:function(value){
			
			return value.substr(0,3)+'****'+value.substr(-3)
		}
	},
	props:{
		title:{
			type: String,
			default: '请输入'
		},
		phone:{
			type: String,
			default: ''
		},
		email:{
			type: String,
			default: ''
		}
	},
	data() {
		return {
			form:{
				phone: '',
				vCode:''	
			},
			emailCode:'',
			
			bannerShow:false,
			content: '',
			dataIndex: 1,
			showBotton: true,
			count: '',
			timer: null,
		};
	},
	mounted:function(){
		this.form.phone = this.phone
	},
	methods:{
		getCode(){
			console.log('sfwfwef')
			 const TIME_COUNT = 120;
			 if (!this.timer) {
				 this.count = TIME_COUNT;
				 this.showBotton = false;
				 this.timer = setInterval(() => {
			          if (this.count > 0 && this.count <= TIME_COUNT) {
			              this.count--;
			           } else {
						  this.showBotton = true;
						  clearInterval(this.timer);
						  this.timer = null;
				       }
			    }, 1000)
			 }
		},
		async sendVcode(type){
			
			if (this.dataIndex==1){
				if (this.phone == ''){
					uni.showToast({
						title: '請先綁定手機號碼',
						icon: 'none',
						duration: 1000
					});
					return
				}
				this.getCode()
				let data ={
					phone: this.phone
				}
				const res  = await this.$http.get('/user/phone_veri',{data: data})
				uni.showToast({
					title: '發送成功',
					duration:  1000
				});
			}else if(this.dataIndex==2){
				if (this.email == ''){
					uni.showToast({
						title: '請先綁定郵箱號碼',
						icon: 'none',
						duration: 1000
					});
					return
				}
				this.getCode()
				let data ={
					mail: this.email,
					type: 2
				}
				const res  = await this.$http.get('/user/mail_vcode',{data: data})
				uni.showToast({
					title: '發送成功',
					duration:  1000
				});
			}
			
			
		},
		async submit(){
			if (this.dataIndex == 1){
				let data ={
					phone: this.phone,
					vCode: this.form.vCode
				}
				console.log(data)
				const res = await this.$http.get('/user/bind_phone',{data:data})
				this.$emit('input')
				this.bannerShow = false
			}else if (this.dataIndex == 2){
				let data ={
					mail: this.email,
					vCode: this.form.vCode
				}
				console.log(data)
				const res = await this.$http.get('/user/check_vcode',{data:data})
				console.log(res)
				this.$emit('input')
				this.bannerShow = false
			}
			
			
		},
		selectLabel:function(e){
			this.dataIndex = e.target.dataset.index
			if (this.dataIndex == 2){

			}
			if (this.dataIndex == 1){
				
			}	
		},
		closeBanner:function(){
			this.bannerShow=false;
		},
		show:function(){
			this.bannerShow=true;
		},
		confirmBanner:function(e){
			
			this.$emit('input',e)
		
			
			this.bannerShow = false
		}
	}
	
}
